.form {
  display: flex;
  flex-direction: column;
  gap: 32px;

  &__inputs {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__input {
    box-sizing: border-box;
    padding: 14px 16px;
    border-radius: 8px;
    background-color: #F9F9F9;
    border: none;

    &--message {
      height: 120px;
      font-family: Manrope, sans-serif;
      resize: none;

    }

    &::placeholder {
      color: #7E7E83;
      font-size: 14px;
      line-height: 140%;
      letter-spacing: $letter-spacing-15;
    }


  &:hover {
    outline: 1px solid #DBDBDB;
  }

  &:focus {
    outline: 2px solid $primary-color;
  }
  }
}
